import { configure, makeAutoObservable } from 'mobx';
import axios from 'axios';
import { axiosError } from './common.js';

axios.defaults.baseURL = process.env.REACT_APP_BASE_URL;

configure({
  enforceActions: 'never',
  useProxies: 'never'
});

class MembersStore {
  constructor() {
    makeAutoObservable(this);
  }

  members = [];
  member = {
    name: '',
    age: ''
  };

  membersCreate() {
    // this.members.push({
    //   name: this.member.name,
    //   age: this.member.age
    // });
    // this.member.name = '';
    // this.member.age = '';
    // console.log('Done membersCreate', this.members);
    axios
      .post('/api/v1/members', this.member)
      .then((response) => {
        console.log('Done membersCreate', response);
        this.membersRead();
      })
      .catch((error) => {
        axiosError(error);
      });
  }

  membersRead() {
    // this.members = [
    //   {
    //     name: '홍길동',
    //     age: 20
    //   },
    //   {
    //     name: '춘향이',
    //     age: 16
    //   }
    // ];
    // console.log('Done membersRead', this.members);

    axios
      .get('/api/v1/members')
      .then((response) => {
        console.log('Done membersRead', response);
        this.members = response.data.members;
      })
      .catch((error) => {
        axiosError(error);
      });
  }

  membersUpdate(index, member) {
    // this.members[index] = member;
    // console.log('Done membersUpdate', this.members);
    const memberUpdate = {
      index: index,
      member: member
    };
    axios
      .patch('/api/v1/members', memberUpdate)
      .then((response) => {
        console.log('Done membersUpdate', response);
        this.membersRead();
      })
      .catch((error) => {
        axiosError(error);
      });
  }

  membersDelete(index) {
    // this.members.splice(index, 1);
    // console.log('Done membersDelete', this.members);
    const url = '/api/v1/members/' + index;
    axios
      .delete(url)
      .then((response) => {
        console.log('Done membersDelete', response);
        this.membersRead();
      })
      .catch((error) => {
        axiosError(error);
      });
  }
}

const membersStore = new MembersStore();
export default membersStore;
