import Header from './components/Header.js';
import Nav from './components/Nav.js';
import Footer from './components/Footer.js';
import { HashRouter, Switch, Route, Redirect } from 'react-router-dom';
import Members from './components/contents/Members.js';
import Search from './components/contents/Search.js';

function App() {
  return (
    <HashRouter>
      <div>
        <Header></Header>
        <hr />
        <div className="container">
          <Nav></Nav>
          <hr />
          <section className="contents">
            <Switch>
              <Route exact={true} path="members" component={Members} />

              <Route exact={true} path="search" render={(props) => <Search {...props} testProps={true} />} />
              <Redirect to={{ pathname: 'members' }} />
            </Switch>
          </section>
          <hr />
        </div>
        <Footer></Footer>
      </div>
    </HashRouter>
  );
}

export default App;
